import { BrowserRouter } from "react-router-dom";
import Form from "./components/Form";
import "./style.css";

const App = () => {
    return (
        <BrowserRouter>
            <main className="main-container">
                <div className="text-div">
                    <h1>Contratar Seguro</h1>
                    <p>
                        Preencha o formulrio abaixo e um de nossos
                        especialistas entrar em contato com voc de forma
                        rpida.
                    </p>
                </div>
                <Form />
            </main>
        </BrowserRouter>
    );
};

export default App;
